<template>
  <div class="page">
    <div class="page__header">
      <h1>تنظیمات</h1>

      <div class="page__create" @click="showCreateModal = true">
        <addSvg />
        <span>افزودن کلید </span>
      </div>
    </div>
    <div class="mt-6">
      <v-data-table
        :headers="headers"
        :items="settings"
        :items-per-page="5"
        class="elevation-1"
        :loading="loading"
      >
        <template v-slot:item.createdAt="{ item }">
          {{ new Date(item.createdAt).toLocaleTimeString("fa-IR") }}
          {{ new Date(item.createdAt).toLocaleDateString("fa-IR") }}
        </template>
        <template v-slot:item.operation="{ item }">
          <v-btn color="error" x-small outlined @click="onDeleteClicked(item)"
            >حذف</v-btn
          >
        </template>
      </v-data-table>
    </div>

    <base-modal
      :show="showCreateModal"
      @close="showCreateModal = false"
      :maxWidth="500"
      title="ایجاد کلید"
    >
      <div>
        <ValidationObserver ref="form" v-slot="{ handleSubmit }">
          <form class="form" @submit.prevent="handleSubmit(add)">
            <v-row>
              <v-col cols="12" sm="12" md="6">
                <ValidationProvider
                  name="کلید"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <v-text-field
                    label="کلید"
                    variant="underlined"
                    v-model="model.key"
                    :error-messages="errors[0]"
                  ></v-text-field>
                </ValidationProvider>
              </v-col>
              <v-col cols="12" sm="12" md="6">
                <ValidationProvider
                  name="مقدار"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <v-text-field
                    label="مقدار"
                    variant="underlined"
                    v-model="model.value"
                    :error-messages="errors[0]"
                  ></v-text-field>
                </ValidationProvider>
              </v-col>
            </v-row>
            <v-col cols="12" sm="12">
              <v-btn color="primary" block type="submit" :loading="addLoading"
                >ثبت</v-btn
              >
            </v-col>
          </form>
        </ValidationObserver>
      </div>
    </base-modal>

    <confirmModal
      :show="ShowConfirmModal"
      title="حذف کلید"
      message="آیا می خواهید کلید را حذف کنید"
      @cancel="ShowConfirmModal = false"
      @close="ShowConfirmModal = false"
      @confirm="deleteKeyConfirm()"
    ></confirmModal>
  </div>
</template>

<script>
import axios from "axios";
import confirmModal from "@/components/Dashboard/modal/confirm.modal.vue";
import addSvg from "@/assets/svg/ic_add-circle.svg";
import BaseModal from "@/components/Dashboard/modal/modal.component.vue";
import { ValidationObserver, ValidationProvider } from "vee-validate";

export default {
  name: "settings",
  components: {
    confirmModal,
    addSvg,
    BaseModal,
    ValidationObserver,
    ValidationProvider,
  },
  data() {
    return {
      loading: false,
      settings: [],
      headers: [
        {
          text: "کلید",
          align: "center",
          sortable: false,
          value: "key",
        },
        {
          text: "مقدار",
          align: "center",
          sortable: false,
          value: "value",
        },
        {
          text: "تاریخ ایجاد",
          align: "center",
          sortable: false,
          value: "createdAt",
        },
        {
          text: "عملیات",
          align: "center",
          sortable: false,
          value: "operation",
        },
      ],
      selected: {},
      ShowConfirmModal: false,
      showCreateModal: false,
      model: {
        key: "",
        value: "",
      },
      addLoading: false,
    };
  },
  mounted() {
    this.getOptions();
  },
  watch: {
    showCreateModal(val) {
      if (!val) {
        this.model = {
          key: "",
          value: "",
        };
      }
    },
  },
  methods: {
    async getOptions() {
      this.loading = true;
      try {
        const { data } = await axios({
          method: "GET",
          url: "/options/admin",
          headers: {
            Authorization: "Bearer " + this.$cookies.get("admin-token"),
          },
        });
        this.settings = data;
      } catch {
        this.$toast.error("خطایی رخ داد");
      } finally {
        this.loading = false;
      }
    },
    onDeleteClicked(item) {
      this.selected = item;
      this.ShowConfirmModal = true;
    },
    async deleteKeyConfirm() {
      try {
        await axios({
          method: "DELETE",
          url: "/options/" + this.selected.key,
          headers: {
            Authorization: "Bearer " + this.$cookies.get("admin-token"),
          },
        });
        this.getOptions();
        this.ShowConfirmModal = false;
      } catch {
        this.$toast.error("خطایی رخ داد");
      }
    },
    async add() {
      this.addLoading = true;
      try {
        await axios({
          method: "POST",
          url: "/options",
          headers: {
            Authorization: "Bearer " + this.$cookies.get("admin-token"),
          },
          data: this.model,
        });

        this.getOptions();
        this.showCreateModal = false;
      } catch {
        this.$toast.error("خطایی رخ داد");
      } finally {
        this.addLoading = false;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.page {
  &__header {
    display: flex;
    justify-content: space-between;
  }
  &__create {
    display: flex;
    align-items: center;
    color: var(--primary-lighter);
    cursor: pointer;
    &:hover {
      transform: scale(1.05);
      transition: all 0.5s;
    }
  }
}
</style>